.Modal {
  display: none;
}
.Modal.active {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow: auto;
  z-index: 300;
  background-color: rgba(51, 51, 51, 0.5333333333);
  display: block;
}
.Modal .Card {
  margin: 1rem auto;
}

.spinner {
  animation: spinner 0.75s ease-in-out infinite;
}

.card:hover .card-img {
  filter: brightness(75%);
}

